import { useCallback, useState } from "react";
import { IRetailer } from "src/mapitApiTypes";

type GetRetailersParams = {
  centerId: string | number,
  apiURI?: string,
}

export type Data = {
  isError: boolean,
  error: string | null,
  items: IRetailer[],
  count: number,
}

const useGetRetailersBySearch = (params: GetRetailersParams) => {
  const { centerId, apiURI } = params;

  const fetchRetailers = useCallback((search: string, limit?: number): Promise<Data> => {
    const r = /\/$/;
    const uri = apiURI?.replace(r, '');
    let page = 1;

    const resultData: { items: IRetailer[], count: number } = { items: [], count: 0 };

    const fetchAll = (): Promise<Data> => new Promise((resolve, reject) => {

      const url = `${uri}/v1/retailers?limit=${limit ? limit : 100}&page=${page ? page : 1}&search=${search ? search : ''}`;

      fetch(url, {
        headers: {
          center_id: centerId.toString(),
        },
      }).then(response => {
        if (!response.ok) {
          reject(`Server error: [${response.status}] [${response.statusText}] [${response.url}]`);
        } else {
          response.json().then(retailers => {
            resultData.items = [...resultData.items, ...retailers.items];
            resultData.count = retailers.count;
            if (resultData.count > resultData.items.length) {
              page = page + 1;
              fetchAll().then(resData => {
                resolve({
                  isError: false,
                  error: null,
                  items: [...resultData.items, ...resData.items],
                  count: resData.count,
                });
              }).catch((e: any) => {
                reject(e.message ? e.message : 'useGetRetailer: something went wrong!');
              })
            } else {
              resolve({
                isError: false,
                error: null,
                items: retailers.items,
                count: retailers.count,
              });
            }
          });
        }
      }).catch((e: any) => {
        reject(e.message ? e.message : 'useGetRetailer: something went wrong!');
      })
    });

    return fetchAll();

  }
    , [centerId, apiURI]);

  return fetchRetailers;
}

export default useGetRetailersBySearch;
